import React from 'react'
import WaterWave from 'react-water-wave';


export default function Herosection(props) {
  return (
    <>
      <div className='btn-outer'>
        <div className='btn-inner'>
          <div className='both-botton'>

            <button type="button" className="btn" onClick={() => { props.toggleMode() }} style={{ background: props.theme === 'light' ? '#0C073E' : '#fff', border: props.theme === 'light' ? '1px solid #0c073e' : '1px solid #fff', color: props.theme === 'light' ? '#fff' : '#0c073e' }}>{props.text}</button>
          </div>
        </div>
      </div>
      <WaterWave>
        {methods => (
          <div className='hero-section-outer'>
            <div className='hero-section-inner'>
              <div className="hero-section-first-div">

                <p style={{ color: props.theme === 'dark' ? "#fff" : "#F24B74" }}>loream ipsum</p>

              </div>
              <div className='hero-section-heading'>
                <h1 style={{ color: props.theme === 'dark' ? "#fff" : "#000" }}><span id='welcome-span-text'
                >welcome</span> to<br />sparkweb<br />solutions</h1>

              </div>

            </div>

          </div>)}
      </WaterWave>


    </>
  )
}


